.Header{
position: sticky;
z-index: 10;
top: 0px;
margin: 0;
padding: 20px;
font-size: 1.5em;
background-color: rgb(198, 228, 116);
display: flex;
align-items: center;

    ul{
        margin: 0px 0 5px 0;
        list-style: none;
        display: flex;
        font-size: 0.8em;

        li{
            cursor: pointer;
            
            &:hover{color: white;}

            // & = ou je suis (li) + ce qui est indiquer (:not(:first-of-type)
            &:not(:last-of-type){
                padding-right: 10px;
                border-right: 2px solid white;
            }

            &:not(:first-of-type){
                padding-left: 10px;
            }
        }

        &.navPrincipale{
            margin: 0 20px 0 50px;
        }

        &.navUtilisateur{
            margin-left: auto;
        }
    }
}